import React from "react";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import './DayTime.css';

const DayTime = (props) => {
    return (
        <Container className="m-b20">
            <Row >
                <Col xs="12" md="12">
                    <p className="m20-0 day-tittle block-title"> </p>
                </Col>
                <Col xs="12" md="4" className="day-border">
                    <Row>
                        <Col md="4" className="day-time">
                            08:00 10:00
                                </Col>
                        <Col md="8">
                           : , , , .
                                </Col>
                    </Row>
                </Col>
                <Col xs="12" md="4" className="day-border">
                    <Row>
                        <Col md="4" className="day-time">
                            10:15 13:45
                                </Col>
                        <Col md="8">
                        ,  ,  
                                </Col>
                    </Row>
                </Col>
                <Col xs="12" md="4" className="day-border">
                    <Row>
                        <Col md="4" className="day-time">
                            14:00 15:25
                                </Col>
                        <Col md="8">
                        ,  , 
                                </Col>
                    </Row>
                </Col>
                <Col xs="12" md="4" className="day-border">
                    <Row>
                        <Col md="4" className="day-time">
                            16:00 19:00
                                </Col>
                        <Col md="8">
                        , ,  , .  ,  .
                                </Col>
                    </Row>
                </Col>
                <Col xs="12" md="4" className="day-border">
                    <Row>
                        <Col md="4" className="day-time">
                            19:00 21:45
                                </Col>
                        <Col md="8">
                        .  . . .  / 
                                </Col>
                    </Row>
                </Col>
                <Col xs="12" md="4" className="day-border">
                    <Row>
                        <Col md="4" className="day-time">
                            21:45 22:30
                                </Col>
                        <Col md="8">
                         ,    , 
                                </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
    );
}

export default DayTime;
